

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
	background-color: white;
}

h1, li a {
	font-family: 'Josefin Slab', serif;
	text-align: center;
}

.head {
    background: black;
    color: white;
    padding: 7px;
	margin: 7px 0;
    border-radius: 7px;
}

input[name="search"] {
    width: 100%;
    padding: 7px 0;
    margin: 7px auto;
    text-align: center;
    border: 2px solid black;
    border-radius: 7px;
}
.select span {
    display: inline-block;
    margin: 0;
    padding: 0;
    width: 50%;
}
.select select {
    width: 99%;
    padding: 7px;
    margin: 7px 0;
    border: 2px solid black;
	border-radius: 7px;
	float: right;
}
.select span:first-of-type select {
    float: left;
}

.select select:focus {
    outline: none;
}

table {
    width: 100%;
    background: #fff;
	color: #000;
	border-radius: 7px;
	border: 2px solid black;

}

thead {
    display: table-header-group;
    vertical-align: middle;
    border-color: inherit;
}
th {
	border-bottom: 1px solid black;
	padding: 7px;
}
thead tr:hover {
	background: black;
	color: white;
}
tbody tr {
    border-left: 1px solid #fff;
	border-right: 1px solid #fff;
}
tbody tr:hover {
	background: black;
	color: white;
    transition: all 0.2s ease-in  0s;
}

td {
	padding: 7px;
}

.container {
	max-width: 1200px;
	margin: 0 auto;
}

ul.pagination {
    width: 203px;
	margin: 0 auto;
	padding: 0;
}

.pagination li {
    display: inline-block;
    padding: 7px;
	background: white;
	border-radius: 7px;
	margin: 7px;
	cursor: pointer;
}
.pagination.load li:first-child, .pagination li.active {
	background: black;
}
.pagination.load li:first-child a, .pagination li.active a {
	color:white;
}
.pagination a {
	padding: 7px;
	text-decoration: none;
}

@media 
only screen and (max-width: 760px),
(min-device-width: 768px) and (max-device-width: 1024px)  {
	table { 
		width: 100%; 
		border-collapse: collapse; 
	  }
	  /* Zebra striping */
	  tr:nth-of-type(odd) { 
		background: #eee; 
	  }
	  th { 
		background: #333; 
		color: white; 
		font-weight: bold; 
	  }
	  td, th { 
		padding: 6px; 
		border: 1px solid #ccc; 
		text-align: left; 
	  }
	/* Force table to not be like tables anymore */
	table, thead, tbody, th, td, tr { 
		display: block; 
	}
	
	/* Hide table headers (but not display: none;, for accessibility) */
	thead tr { 
		position: absolute;
		top: -9999px;
		left: -9999px;
	}
	
	tr { border: 1px solid #ccc; }
	
	td { 
		/* Behave  like a "row" */
		border: none;
		border-bottom: 1px solid #eee; 
		position: relative;
		padding-left: 50%; 
	}
	
	td:before { 
		/* Now like a table header */
		position: absolute;
		/* Top/left values mimic padding */
		top: 6px;
		left: 6px;
		width: 45%; 
		padding-right: 10px; 
		white-space: nowrap;
	}
	
	/*
	Label the data
	*/
	td:nth-of-type(1):before { content: "Restaurant"; }
	td:nth-of-type(2):before { content: "Address"; }
	td:nth-of-type(3):before { content: "City"; }
	td:nth-of-type(4):before { content: "State"; }
	td:nth-of-type(5):before { content: "Telephone"; }
	td:nth-of-type(6):before { content: "Genre"; }

}